        <div class="box col-xs-12" bind:offsetHeight="{box1Height}">
            <h1>Nalara Store</h1>
            <h3>Password Recovery</h3>
            <p>Enter your email to recover your password</p>
            <div class="inp">
                <svg style="bottom:18px" width="22" height="17" viewBox="0 0 28 22" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M26.6667 0H1.77778C1.30628 0 0.854097 0.187301 0.520699 0.520699C0.187301 0.854097 0 1.30628 0 1.77778V19.5556C0 20.0271 0.187301 20.4792 0.520699 20.8126C0.854097 21.146 1.30628 21.3333 1.77778 21.3333H26.6667C27.1382 21.3333 27.5903 21.146 27.9237 20.8126C28.2571 20.4792 28.4444 20.0271 28.4444 19.5556V1.77778C28.4444 1.30628 28.2571 0.854097 27.9237 0.520699C27.5903 0.187301 27.1382 0 26.6667 0ZM25.2978 19.5556H3.25333L9.47555 13.12L8.19556 11.8844L1.77778 18.5244V3.12889L12.8267 14.1244C13.1598 14.4556 13.6103 14.6414 14.08 14.6414C14.5497 14.6414 15.0002 14.4556 15.3333 14.1244L26.6667 2.85333V18.4089L20.1244 11.8667L18.8711 13.12L25.2978 19.5556ZM2.94222 1.77778H25.2267L14.08 12.8622L2.94222 1.77778Z" fill="currentColor"/>
                </svg>    
                <input on:focus="{e => focusEv(e)}" on:focusout="{e => focusEv(e)}" type="email" autocomplete="off" placeholder="Email Address" bind:value="{email}" />
            </div>
        </div>
    </div>
    <div class="box btn">
        <button class="row middle-xs center-xs" on:click="{e => {createRipple(e, "rgba(34, 145, 255, 0.2)");fPass()}}">
            Continue
        </button>
    </div>
</div>

