<script>
    import { onMount } from "svelte";
    onMount(()=>{
        document.querySelector(".full").style.minHeight = document.querySelector(".full").offsetHeight + "px";
    })
    import { link, navigate } from "svelte-routing";

    let inp1, inp2, inp3, inp4;
    let i1, i2, i3, i4;
    let verify = () => {
        navigate("/reset-password/kodepanjang")
        // alert("Sorry, under construction !!!");
    }
    
    let in1 = () => {
        if(i1 == null){
            i1 = "";
            inp1.focus()
        } else if(i1 > 9){
            i1 = parseInt(i1.toString().slice(0, -1));
        } else if(i1 != null) {
            inp1.disabled = true
            inp2.disabled = false
            inp2.focus()
        }
    }
    let in2 = () => {
        if(i2 == null){
            i2 = "";
            inp2.focus()
        } else if(i2 > 9){
            i2 = parseInt(i2.toString().slice(0, -1));
        } else if(i2 != null) {
            inp2.disabled = true
            inp3.disabled = false
            inp3.focus()
        }
    }
    let in3 = () => {
        if(i3 == null){
            i3 = "";
            inp3.focus()
        } else if(i3 > 9){
            i3 = parseInt(i3.toString().slice(0, -1));
        } else if(i3 != null) {
            inp3.disabled = true
            inp4.disabled = false
            inp4.focus()
        }
    }
    let in4 = () => {
        if(i4 == null){
            i4 = "";
            inp4.focus()
        } else if(i4 > 9){
            i4 = parseInt(i4.toString().slice(0, -1));
        }
    }

    let key;
    let din1 = (e) => {
        key = e.keyCode || e.charCode;
        if(key === 8 || key === 46){
            // nothing
        }
    }
    let din2 = (e) => {
        key = e.keyCode || e.charCode;
        if(key === 8 || key === 46){
            if(i2 == null || i2 == ""){
                inp1.disabled = false;
                inp2.disabled = true;
                inp1.focus();
            }
        }
    }
    let din3 = (e) => {
        key = e.keyCode || e.charCode;
        if(key === 8 || key === 46){
            if(i3 == null || i3 == ""){
                inp2.disabled = false;
                inp3.disabled = true;
                inp2.focus();
            }
        }
    }
    let din4 = (e) => {
        key = e.keyCode || e.charCode;
        if(key === 8 || key === 46){
            if(i4 == null || i4 == ""){
                inp3.disabled = false;
                inp4.disabled = true;
                inp3.focus();
            }
        }
    }
    
    function createRipple(event, color) {
        const button = event.currentTarget;
        const pos = button.getBoundingClientRect();
        const circle = document.createElement("span");

        circle.style.background = color;
        circle.style.top = (event.clientY - (button.offsetHeight / 2 + pos.top)) - 124 + "px";
        circle.style.left = (event.clientX - (button.offsetWidth / 2 + pos.left)) + "px";
        circle.classList.add("ripple");

        const ripple = button.getElementsByClassName("ripple")[0];

        if (ripple) {
            ripple.remove();
        }

        button.appendChild(circle);
    }
    let box1Height = 0
</script>

<svelte:head>
    <title>Password Recovery Verification - Nalara Store</title>
    <meta content="#2291FF" name="theme-color">
    <style>
        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
            /* display: none; <- Crashes Chrome on hover */
            -webkit-appearance: none;
            margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
        }

        input[type=number] {
            -moz-appearance:textfield; /* Firefox */
        }
    </style>
</svelte:head>

<div class="full" style="min-height:{box1Height + 134 - 20}px">
    <div class="box form row middle-xs center-xs">
        <div class="box col-xs-12" bind:offsetHeight="{box1Height}">
            <h1>Nalara Store</h1>
            <h3>Verification</h3>
            <p>We have sent a code to your email<br>nalara.art@gmail.com</p>
            <div class="inp row between-xs">
                <input type="number" bind:this="{inp1}" bind:value="{i1}" class="col-xs" on:input="{in1}" on:keydown="{e => din1(e)}">
                <input type="number" bind:this="{inp2}" bind:value="{i2}" class="col-xs" on:input="{in2}" on:keydown="{e => din2(e)}" disabled>
                <input type="number" bind:this="{inp3}" bind:value="{i3}" class="col-xs" on:input="{in3}" on:keydown="{e => din3(e)}" disabled>
                <input type="number" bind:this="{inp4}" bind:value="{i4}" class="col-xs" on:input="{in4}" on:keydown="{e => din4(e)}" disabled>
            </div>
        </div>
    </div>
    <div class="box btn">
        <button class="row middle-xs center-xs" on:click="{e => {createRipple(e, "rgba(34, 145, 255, 0.2)");verify()}}">
            Continue
        </button>
    </div>
</div>

